import React, { useState, useEffect, useRef } from 'react';
import type { NextPage } from 'next';
import Profile from '../Profile';
import { ButtonLoading, ICON_NAMES, SVGIcon } from '@react-page/editor';
import Cookies from 'js-cookie';
import { toast } from 'react-toastify';
import { useRouter } from 'next/router';
import type { siteInfoType } from '../../types';
import ScreenLoading from '../screenLoading';

const GoLive: NextPage = () => {
  const router = useRouter();
  const [loading, setLoading] = useState(false);
  const [open, setOpen] = useState(false);
  const [selectedSite, setSelectedSite] = useState('');
  const wrapperRef = useRef<HTMLDivElement>(null);
  const [sites, setSites] = useState<Array<siteInfoType>>([]);
  const [pageLoad, setPageLoad] = useState(false);
  const [type, setType] = useState('');

  const golive = async () => {
    const data = {
      name: 'mintstream-' + Cookies.get('_id'),
      description:
        "Introducing MINTSTREAM live-streaming! Share your screen, bring fans up, give live performances, teach, host live polls, premiere music videos, and even draw your concepts on the screen using our 'whiteboard' feature. Get creative! In-stream tipping coming soon:)",
      template_id: process.env.NEXT_PUBLIC_LIVESTREAM_ID,
      region: '',
      userId: Cookies.get('_id'),
    };
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
      body: JSON.stringify(data),
    };

    try {
      await fetch(`${process.env.NEXT_PUBLIC_SERVER_API_URL}/rooms`, options)
        .then((res) => res.json())
        .then((res) => {
          if (res.code === 200) {
            window.open(selectedSite + '/preview/broadcaster', '_blank');
          } else {
            toast.error(res.message);
          }
          setLoading(false);
        });
    } catch (error) {
      toast.error('Network Error!');
      setLoading(false);
    }
  };

  const goaudio = async () => {
    const data = {
      name: 'mintchat-' + Cookies.get('_id'),
      description:
        "Take your connections to the next level with MINTCHAT live audio chat rooms. Hosting listening parties, Q&A sessions, or giving BTS looks at your creative process. With the ability to invite fans and other artists up to join the conversation, it's like your own interactive talk show!",
      template_id: process.env.NEXT_PUBLIC_AUDIOCHAT_ID,
      region: '',
      userId: Cookies.get('_id'),
    };
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
      body: JSON.stringify(data),
    };

    try {
      await fetch(`${process.env.NEXT_PUBLIC_SERVER_API_URL}/rooms`, options)
        .then((res) => res.json())
        .then((res) => {
          if (res.code === 200) {
            window.open(selectedSite + '/audio/speaker', '_blank');
          } else {
            toast.error(res.message);
          }
          setLoading(false);
        });
    } catch (error) {
      toast.error('Network Error!');
      setLoading(false);
    }
  };

  const data = [
    {
      icon: <SVGIcon name={ICON_NAMES.LIVESTREAM} />,
      subtitle: 'Livestream',
      title: 'STREAM',
      button: (
        <button
          onClick={() => {
            setOpen(true);
            setType('livestream');
          }}
          className="flex flex-row bg-primary-blue text-gray-3 h-[64px] px-[32px] w-full md:w-[260px] items-center gap-[12px] justify-center rounded-[20px] opacity-100 hover:opacity-60  mt-[30px] md:mt-[55px]"
        >
          <SVGIcon name={ICON_NAMES.VIDEO1} />
          <div className="text-[18px]">Launch livestream</div>
        </button>
      ),
      description:
        'Introducing MINTSTREAM live-streaming! Share your screen, bring fans up, give live performances, teach, host live polls, premiere music videos, and even draw your concepts on the screen using our "whiteboard" feature. Get creative! In-stream tipping coming soon:)',
    },
    {
      icon: <SVGIcon name={ICON_NAMES.LIVECHAT} />,
      subtitle: 'Live audio chat',
      title: 'CHAT',
      button: (
        <button
          onClick={() => {
            setOpen(true);
            setType('audio');
          }}
          className="flex flex-row bg-primary-blue text-gray-3 h-[64px] px-[32px] w-full md:w-[260px] items-center gap-[12px] justify-center rounded-[20px] opacity-100 hover:opacity-60 mt-[30px] md:mt-[55px]"
        >
          <SVGIcon name={ICON_NAMES.MIC} />
          <div className="relative text-[18px]">Launch audio chat</div>
        </button>
      ),
      description:
        "Take your connections to the next level with MINTCHAT live audio chat rooms. Hosting listening parties, Q&A sessions, or giving BTS looks at your creative process. With the ability to invite fans and other artists up to join the conversation, it's like your own interactive talk show!",
    },
  ];

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const { current: wrap } = wrapperRef!;
      if (wrap && !wrap.contains(event.target as Node)) {
        setOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [wrapperRef]);

  const goroom = () => {
    setLoading(true);
    if (type === 'livestream') {
      golive();
    } else {
      goaudio();
    }
  };

  const goOnboarding = () => {
    router.push({
      pathname: '/onboarding',
    });
  };

  useEffect(() => {
    getSites();
  }, []);

  const getSites = async () => {
    const options = {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
    };

    try {
      await fetch(
        `${process.env.NEXT_PUBLIC_SERVER_API_URL}/sites?userId=${Cookies.get(
          '_id'
        )}`,
        options
      )
        .then((res) => res.json())
        .then((data) => {
          if (data.code === 200) {
            setSites(data.data);
          } else {
            toast.error(data.message);
          }
          setPageLoad(true);
        });
    } catch (error) {
      setPageLoad(true);
      toast.error('Network Error!');
    }
  };

  useEffect(() => {
    if (sites.length !== 0) {
      setSelectedSite(sites[0].subdomain);
    }
  }, [sites]);

  if (!pageLoad) {
    return <ScreenLoading />;
  }

  return (
    <div className="flex-1 h-[100vh] overflow-y-auto flex flex-col p-[24px] pb-[96px] md:p-[64px] box-border items-start justify-start gap-[30px] md:gap-[64px]">
      <div className="self-stretch flex flex-col md:flex-row items-start justify-start gap-[20px] md:gap-[64px]">
        <div
          className="flex flex-row items-center w-full md:w-fit md:order-last justify-start gap-[32px] text-lg"
          // onClick={() => setPage(3)}
        >
          <Profile />
        </div>
        <div className="flex-1 flex flex-col items-start justify-start gap-[8px]">
          <div className="self-stretch relative tracking-[-0.01em] leading-[30px] md:leading-[60px] text-[30px] md:text-[48px] font-bold text-gray-2">
            It’s show time!
          </div>
          <div className="self-stretch relative text-[14px] md:text-[20px] leading-[30px] text-gray-1">
            Engage with your fans on a deeper level. Choose from two to go live!
          </div>
        </div>
      </div>
      <div className="grid grid-cols-1 xl:grid-cols-2 gap-[20px] md:gap-[64px]">
        {data.map((item, index) => (
          <div
            key={index}
            className="flex flex-col p-[35px] md:p-[48px] bg-alpha-10 rounded-[15px] overflow-hidden"
          >
            {item.icon}
            <div className="text-gray-1 text-[12px] md:text-[14px] mt-[11px] md:mt-[15px]">
              {item.subtitle}
            </div>
            <div className="text-gray-2 text-[30px] md:text-[36px] mt-[11px] md:mt-[15px]">
              MINT<span className="text-primary-mint">{item.title}</span>
            </div>
            <div className="text-gray-1 text-[14px] md:text-[16px] mt-[11px] md:mt-[15px]">
              {item.description}
            </div>
            {item.button}
          </div>
        ))}
      </div>
      <div className="text-alpha-30 text-[14px]">
        {`Pro-tip: Fans will see a notification banner at the top of your website
        when you go live. Text notifications coming soon:)`}
      </div>
      <div
        className={
          'absolute flex top-0 left-0 w-[100vw] h-[100vh] bg-[#00000099] items-center justify-center z-20 backdrop-blur-[20px] ' +
          (open === true ? '' : 'hidden')
        }
      >
        <div
          ref={wrapperRef}
          className={
            'relative flex flex-col bg-gray-4 w-[360px] z-20 rounded-[12px] p-[24px] gap-[20px]'
          }
        >
          <div className="text-gray-2 text-[20px] leading-[18px]">
            Select Your Site
          </div>
          <div className="text-gray-1 text-[14px] leading-[18px]">
            Select the site you want to go live from
          </div>
          <hr className="w-full h-px bg-gray-1 border-[1px]" />
          {sites.length !== 0 && (
            <select
              value={selectedSite}
              onChange={(e) => setSelectedSite(e.target.value)}
              className="h-[40px] bg-alpha-10 text-gray-2 text-sm rounded-lg focus:outline-none focus:border-solid border-[1px] border-gray-2 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 custom-select"
            >
              {sites.map((site, index) => (
                <option
                  key={index}
                  value={site.subdomain}
                  className="text-gray-2 bg-gray"
                >
                  {site.name}
                </option>
              ))}
            </select>
          )}
          <button
            onClick={() => goOnboarding()}
            className="flex items-center justify-center gap-[10px] w-full text-gray-2 bg-alpha-10 h-[48px] rounded-[8px] text-[14px] opacity-100 hover:opacity-60"
          >
            <div className="flex items-center justify-center gap-[10px] h-full no-underline">
              Create new site
            </div>
            <SVGIcon name={ICON_NAMES.PLUS} />
          </button>
          {sites.length !== 0 && (
            <button
              onClick={() => goroom()}
              className="flex items-center justify-center gap-[10px] w-full text-gray-2 bg-primary-blue h-[40px] rounded-[8px] px-5 text-[14px] opacity-100 hover:opacity-60"
            >
              {loading ? (
                <ButtonLoading />
              ) : (
                <div className="flex items-center justify-center gap-[10px] w-full h-full">
                  Go live
                </div>
              )}
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default GoLive;
