import React from 'react';
import type { NextPage } from 'next';
import Golive from '../../components/dashboardContent/golive';
import DashboardLayout from '../../components/DashboardLayout';

const DashboardGolive: NextPage = () => {
  return (
    <DashboardLayout page={'golive'}>
      <Golive />
    </DashboardLayout>
  );
};

export default DashboardGolive;
